// src/components/ReviewSection.tsx - Kullanıcı Yorumları Bölümü
'use client';

import { StarIcon } from '@heroicons/react/24/solid';

interface Review {
  name: string;
  city: string;
  text: string;
  rating: number;
}

const reviews: Review[] = [
  {
    name: 'Anna S.',
    city: 'München',
    text: 'Sehr schnelle Hilfe! Innerhalb von 20 Minuten war der Techniker da und hat meine Tür beschädigungsfrei geöffnet.',
    rating: 5,
  },
  {
    name: 'Markus T.',
    city: 'Dachau',
    text: 'Transparente Preise und super freundlich. Ich musste nachts Hilfe rufen und es hat alles perfekt geklappt.',
    rating: 5,
  },
  {
    name: 'Julia K.',
    city: 'Augsburg',
    text: 'Mein Autoschlüssel war verloren. Schlüsselmann hat direkt vor Ort einen neuen programmiert. Top Service!',
    rating: 5,
  },
];

export default function ReviewSection({ cityColorClass }: { cityColorClass: string }) {
  return (
    <section className="mt-16 mb-20">
      <h2 className="text-2xl md:text-3xl font-bold text-center text-gray-800 mb-8">
        Was unsere Kunden sagen
      </h2>
      <div className="grid md:grid-cols-3 gap-8">
        {reviews.map((review, idx) => (
          <div
            key={idx}
            className="p-6 border rounded-xl shadow-md bg-white hover:shadow-lg transition duration-300"
          >
            <div className="flex items-center mb-3">
              {[...Array(review.rating)].map((_, i) => (
                <StarIcon key={i} className={`h-5 w-5 ${cityColorClass}`} />
              ))}
            </div>
            <p className="text-gray-700 italic mb-4">"{review.text}"</p>
            <p className="text-sm font-semibold text-gray-900">
              {review.name} – {review.city}
            </p>
          </div>
        ))}
      </div>
    </section>
  );
}
